export default {
	data() {
		return {
			address: 0, // 0没有地址1有地址
			addressdata: '',
			goodsRow: '',
			tkstatus: false,
			tiaokuanstatus: 2, // 条款弹窗 1显示 2隐藏
			options: '',
			userinfo: '',
			
			// 规格
			specs: '',
			specs_index1: null,
			specs_index2: null,
			specs1: '',
			specs2: '',
			goods_sku_id: 0,
			animagg2: false,
		}
	},

	//监听页面加载，其参数为上个页面传递的数据，参数类型为Object（用于页面传参）
	onLoad(options) {
		var that = this;
		that.tkstatus = uni.getStorageSync('tkstatus') ? uni.getStorageSync('tkstatus') : false;

		if (!options.goods_id) {
			uni.showModal({
				title: '温馨提示',
				content: '缺少关键参数',
				showCancel: false,
				success() {
					uni.navigateBack({})
				},
			})
			return;
		}
		that.userinfo = uni.getStorageSync('userinfo') ? uni.getStorageSync('userinfo') : uni.getStorageSync('userInfo')?uni.getStorageSync('userInfo'):'';
		that.options = options;

		that.specs_index1 = options.specs_index1;
		that.specs_index2 = options.specs_index2;
		that.goods_sku_id = options.sku_id;
		this.$http.post("goods/specs",{goods_id:options.goods_id}).then((res)=>{
			that.specs = res.data.specs;
			if (options.skuname){
				that.specs1 = options.specs1;
				that.specs2 = options.specs2;
			}else{
				that.specs1 = res.data.specs.specs1[options.specs_index1].id;
				that.specs2 = res.data.specs.specs2[options.specs_index2].id;
			}
			
		})
		
	},
	//监听页面显示。页面每次出现在屏幕上都触发，包括从下级页面点返回露出当前页面
	onShow() {
		var that = this;
		var address_id = uni.getStorageSync('address_id');

		if (address_id) {
			that.$http.post("address/getAddress", {
				address_id: address_id
			}).then((res) => {
				console.log('获取收货地址')
				console.log(res);
				if (res.data != '') {

					that.addressdata = res.data;
					that.address = 1;

				}
			})
			// that.get_data();

		} else {
			that.$http.post("address/getMorenAddress").then((res) => {

				if (res.data) {
					uni.setStorageSync('address_id', res.data.id);

					that.addressdata = res.data;
					that.address = 1;

				}

			})
			// that.get_data();
		}
	},
	//监听页面初次渲染完成。注意如果渲染速度快，会在页面进入动画完成前触发
	onReady() {},
	//监听页面隐藏
	onHide() {},
	//监听页面卸载
	onUnload() {},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {},
	methods: {
		tiaokuanalert(e) {
			var that = this;
			if (that.tiaokuanstatus == 1) {
				that.tiaokuanstatus = 2;
			} else {
				that.tiaokuanstatus = 1;
			}
		},
		tiaokuan(e) {
			var that = this;
			if (that.tkstatus == false) {
				that.tkstatus = true;
			} else {
				that.tkstatus = false;
			}
			uni.setStorageSync('tkstatus', that.tkstatus);
			console.log(that.tkstatus);
		},
		pay(res) {
			var that = this;
			if (res.detail.userInfo) {
				uni.setStorageSync('userInfo', res.detail.userInfo);
				that.userinfo = res.detail.userInfo;
				this.upUserInfo(res.detail);
			}

			if (that.userinfo == '') {
				uni.showModal({
					title: '温馨提示',
					content: '请授权后进行付款',
					showCancel: false,
					success() {},
				})
				return false;
			}
			console.log('可以进行付款')
			if (that.tkstatus == false) {
				uni.showModal({
					title: '温馨提示',
					content: '请阅读《拼团夺宝商品购买条款》后进行购买',
					showCancel: false,
					success: res => {

					},
				});
				return;
			}
			var address_id = uni.getStorageSync('address_id');
			if (!address_id) {
				uni.showToast({
					title: '请选择收货地址',
				})
				return;
			}
			if (that.options.buytype == 'pin') {
				var pintuan_home_id = null;
			} else {
				var pintuan_home_id = that.options.pintuan_home_id;
			}
			this.$http.post("pintuan/createteam", {
				id: that.options.goods_id,
				address_id: address_id,
				pintuan_home_id: pintuan_home_id,
				sku_id: that.goods_sku_id ? that.goods_sku_id : false,
			}).
			then((res) => {
				console.log('开团', res);
				if (res.code == 0) {
					return;
				}
				var config = res.data.new_res;
				var pintuan_orders_id = res.data.pintuan_orders_id;
				var hb_status = res.data.hb_status;
				uni.requestPayment({
					timeStamp: config.timeStamp,
					nonceStr: config.nonceStr,
					package: config.package,
					signType: config.signType,
					paySign: config.paySign,
					success: function(res) {
						console.log('支付接口')
						console.log(res)
						if (hb_status == 'yes') {
							setTimeout(function() {
								uni.redirectTo({
									url: "/pages/redbag/redbag?pintuan_orders_id=" + pintuan_orders_id
								})
							}, 1000);
						} else {
							setTimeout(function() {
								uni.redirectTo({
									url: "/pages/payment/payment?pintuan_orders_id=" + pintuan_orders_id
								})
							}, 1000);
						}


					},
					fail: function() {

					}
				})
			})
		},
		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
		get_data() {
			var that = this;
			this.$http.post("goods/show", {
				id: that.options.goods_id
			}).then((res) => {
				console.log('获取订单商品信息', res);
				that.goodsRow = res.data;
			})

		},
		
		alertdonghuaout(e) {
			var that = this;

			that.animagg2 = true;
		
		},
		alert_donghua_hide(e) {
			var that = this;

			that.animagg2 = false;
		},
		// 切换规格
		changeguige(e) {
			var that = this;
			var scrollid = e.currentTarget.dataset.scrollid;
			var id = e.currentTarget.dataset.id;
			var group_id = e.currentTarget.dataset.group_id;
			var index = e.currentTarget.dataset.index;
		
			that.specs1 = id;
			that.specs_index1 = index;
			that.specs_index2 = null;
			that.specs2 = 0;
			that.scrollkey = scrollid;
			that.judgeGoodsNum();
		},
		changeguige2(e) {
			var that = this;
			var scrollid = e.currentTarget.dataset.scrollid;
			var id = e.currentTarget.dataset.id;
			var group_id = e.currentTarget.dataset.group_id;
			var index = e.currentTarget.dataset.index;
		
			that.specs2 = id;
			that.specs_index2 = index;
			that.scrollkey = scrollid;
			that.judgeGoodsNum();
		},
		judgeGoodsNum() {
			var that = this;
			var goods_id = that.options.goods_id;
			var specs1 = that.specs1;
			var specs2 = that.specs2 ? that.specs2 : 0;
			this.$http.post("judgeGoodsNum",{goods_id:goods_id,specs1:specs1,specs2:specs2},{showLoading:false}).then((res)=>{
				console.log(res.data.id);
				that.goods_sku_id = res.data.id ? res.data.id : 0;
			})

		},
	}
}
